import React, {Component} from "react";
import {connect} from "react-redux";
import OraDocumentsSectionPart from "./OraDocumentsSectionPart";
import {getDepositCost} from "eshop/modules/cart/selectors";
import {Expander, Section, Toggle} from "eshop/modules/core/components/ui/Expander";

class OraMobileOffersDocumentsSection extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        console.log(this.props.selectedOfferType);
        let regulationsList = this.props.regulationsList;
        let agreementsList = this.props.agreementsList;
        let pricesList = this.props.pricesList;
        let depositList = this.props.depositList;

        const toggleHeader = <h2 className="h4 opl-section__heading">Umowy, cenniki, regulaminy</h2>;
        const sectionHeader =
            <div className="l-row">
                <Toggle variant="arrow" header={toggleHeader} className="l-col l-col-12 " triggerClassName="opl-document__expander__trigger">
                    <span className="opl-section__text-show">rozwiń sekcję Umowy, cenniki, regulaminy</span>
                    <span className="opl-section__text-hide">zwiń sekcję Umowy, cenniki, regulaminy</span>
                </Toggle>
            </div>;

        return (
            <Expander scrollToSelected={false} className="l-page-row" id="doc_expander">
                <Section header={sectionHeader}  variant="section" className="opl-document__expander" contentClassName="opl-document__expander__content" id="document-component-section">
                    <Expander scrollToSelected={false} className="u-padding-left-l" id="doc_section_expander">
                            {
                                !!((this.props.selectedOfferType !== "SIMFREE") &&agreementsList && agreementsList.length != 0) &&
                                    <OraDocumentsSectionPart documentList={agreementsList} header="Umowy"/>
                            }
                            {
                                !!(this.props.selectedOfferType!=="SIMFREE") && !!(pricesList && pricesList.length != 0) &&
                                    <OraDocumentsSectionPart documentList={pricesList} header="Cenniki"/>
                            }
                            {
                                !!(regulationsList && regulationsList.length != 0) &&
                                    <OraDocumentsSectionPart documentList={regulationsList} header="Regulaminy"/>
                            }
                            {
                                !!(this.props.selectedOfferType!=="SIMFREE") && !!(depositList && depositList.length != 0 && this.props.getDepositCost) &&
                                    <OraDocumentsSectionPart documentList={depositList} header="Depozyty"/>
                            }
                    </Expander>
                </Section>
            </Expander>
        )
    }
}

const mapStateToProps = state => ({
    getDepositCost: getDepositCost(state)
})

const mapDispatchToProps = dispatch => ({
})

export default connect(mapStateToProps, mapDispatchToProps)(OraMobileOffersDocumentsSection)

